import React from 'react'
import '../../components/styles/square.less'
import Layout from '../../components/layout'
import PrevNext from '../../components/prevnext'
import HeroImg from '../../images/square-hero.jpg'
import GridImg from '../../images/square-grid.jpg'
import ProcessImg from '../../images/square-process.png'
import SystemImg from '../../images/square-system.png'

const Square = () => (
  <Layout>
    <div className="square-container">
      <section>
        <h1>Squarespace Studio</h1>
        <p className="square-description">
          Squarespace Studio is a stand-alone app that enables users to discover
          and book a professional photographer for showcasing their products
          photography on their personal Squarespace site.
        </p>
        <p>Link to a more detailed Case Study:</p>
      </section>
      <img src={HeroImg} alt="Squarespace Studio project" />
      <section className="square-challenge">
        <div>
          <h2>Challenge</h2>
        </div>
        <div>
          <h5>Problem</h5>
          <p>
            Squarespace Studio is a stand-alone app that enables users to
            discover and book a professional photographer for showcasing their
            products photography on their personal Squarespace site.
          </p>
          <h5>Insight</h5>
          <p>
            Through research I learned that people are often overwhelmed with a
            number of photographers to select from. The most important criteria
            for them is work examples, price and reviews. Also, business owners
            prefer to have a more detailed discussion with a photographer before
            making a decision to book a service.
          </p>
          <h5>Solution</h5>
          <p>
            A native mobile app that allows business owners to post a job and
            get qualified proposals. Users can compare them to pick the perfect
            match, communicate directly with the photographer, and manage the
            job from booking to receiving finished assets and making a payment,
            all in a single location.
          </p>
        </div>
      </section>
      <img src={ProcessImg} alt="design process" />
      <section className="process-challenge">
        <div>
          <h4>Process</h4>
        </div>
        <div>
          <p>
            To get the project started and to inform my process, I did research:
            studied websites and apps for booking product photographers, read
            articles on how to hire a product photographer to understand the
            nuances of the industry, distributed the survey among multiple
            groups of entrepreneurs and interviewed people who had experience
            booking a photographer. I learnt that they prefer to search for pros
            on their own, but often feel overwhelmed with a selection. It is
            important for them to be able to ask questions and be in control of
            the process.
          </p>
        </div>
        <div>
          <p>
            I identified 2 user groups with significantly different motivations
            for using the product: Product Owners and Photographers, but for
            this project focused on the former only. To gain a better
            understanding of the entire process, I mapped out the customer’s
            journey and made a list of tasks the customer needs to be able to
            complete to use our product successfully. This allowed me to think
            about different flows and identify interactions our product needs to
            support. Creating a sitemap helped visually denote how different
            screens and content relate to one another.
          </p>
        </div>
      </section>
      <img src={SystemImg} alt="design system" />
      <section className="process-challenge">
        <div>
          <h4>Design System</h4>
        </div>
        <div>
          <p>
            After sketching ideas on paper and moving to mid-fidelity, I created
            hi-fi prototypes in the style of other Squarespace apps. The brief
            has a lot of questions, and to make it less overwhelming I broke it
            down into multiple shorter and more manageable steps and introduced
            a progress bar to help users feel like they are making progress
            towards an end goal and keep their motivation high. The tab bar
            contains the main pieces of core functionality available with one
            tap, allowing rapid switching between screens.
          </p>
        </div>
        <div>
          <p>
            The card with an offer has all relevant information telling the user
            if this pro is a good match. Images are in the very center to grab
            users’ attention. In the detail view we present more work examples
            loud and clear. The quote and actions are fixed at the bottom of the
            screen. There are multiple ways for users to contact a photographer
            for more details before they decide to commit. Users can track the
            progress of the order, evaluate finished images and pay for the
            services. To improve on my design I would conduct usability testing
            sessions and iterate on the solutions.
          </p>
        </div>
      </section>
      <img src={GridImg} alt="project grid" />
    </div>
  </Layout>
)

export default Square
